
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#app .button-group {
  margin-bottom: 20px;
}
#app .will {
  color: red;
  margin-left: 10px;
}
#app .notDisplay {
  display: none;
}
#app .display {
  display: table;
}
#app .el-table .warning-row {
  background: oldlace;
}
#app .el-table .success-row {
  background: #f0f9eb;
}
#app .row-class:nth-child(1) {
  background: oldlace;
}



